export class SecureTextBox {
    // readonly apiKeyInput: HTMLInputElement;
    readonly form: HTMLFormElement;

    constructor(elementId: string) {
        const apiKeyInput = document.createElement("input") as HTMLInputElement
        apiKeyInput.id = elementId;
        apiKeyInput.className = 'api-key-textarea';
        apiKeyInput.type = 'password';
        apiKeyInput.placeholder = 'Enter your OpenAI API key here. The API key will not be stored or sent anywhere.';
        apiKeyInput.autocomplete='on';

        this.form = document.createElement("form") as HTMLFormElement
        this.form.appendChild(apiKeyInput);
    }
}
