import { GraphDtoModels } from "./graph-dto-models";
import { GraphManager } from "./graph-manager";
import { Node } from "./node";

export class GraphMap {

    map = new Map<number, Node>();
    nodeMap = new Map<Node, Node[]>();
    private grapDto: GraphDtoModels.GraphDto | null = null;

    init(dto: GraphDtoModels.GraphDto) {
        this.grapDto = dto;
        this.createGrpathOnPaper(dto);
    }

    getGraphDto() {
        return this.grapDto;
    }

    deleteNode(node: Node) {
        let id = node.nodeDto.id;
        this.map.delete(id);
        this.deleteNodeFromNodeMap(id);
        this.grapDto!.nodes = this.grapDto?.nodes?.filter(e => e.id !== id);
        this.grapDto!.edges = this.grapDto?.edges?.filter(e => e.nodeFrom !== id && e.nodeTo !== id);
    }

    expandGraph(selectedNodeId: number, json: string) {
        let dto: GraphDtoModels.GraphDto | null = null;
        try {
            dto = GraphDtoModels.GraphDto.fromJsonString(json);
        } catch (e) {
            console.error(e);
            return;
        }

        let firstId = Math.min(...dto.nodes?.map(e => e.id)!);
        let newEdges = [new GraphDtoModels.EdgeDto(selectedNodeId, firstId)];
        dto.nodes?.forEach(e => this.grapDto?.nodes?.push(e));
        newEdges?.forEach(e => dto?.edges?.push(e));
        dto.edges?.forEach(e => this.grapDto?.edges?.push(e));

        this.createGrpathOnPaper(dto);
    }

    private createGrpathOnPaper(dto: GraphDtoModels.GraphDto) {
        let nodes = dto.nodes?.map(e => new Node(this, e));
        nodes?.forEach(e => this.map.set(e.nodeDto.id, e));
        dto.edges?.forEach(e => this.populateMap(this.map, e, e.text));
    }

    private populateMap(map: Map<number, Node>, edge: GraphDtoModels.EdgeDto, text: string | null) {
        let parent = map.get(edge.nodeFrom)!;
        let child = map.get(edge.nodeTo)!;
        this.addEdge(parent, child, text)
    }

    private addEdge(parent: Node, child: Node, text: string | null) {
        if (this.nodeMap.has(parent)) this.nodeMap.get(parent)?.push(child);
        else this.nodeMap.set(parent, [child]);

        this.addLineEdge(parent, child, text);
        parent.init();
        child.init();
    }

    private addLineEdge(parent: Node, e: Node, text: string | null) {
        let p0 = parent.rect.position;
        let p1 = e.rect.position;
        let line = GraphManager.getLine(p0, p1, text);
        parent.addEdge(line.firstSegment);
        e.addEdge(line.lastSegment);
    }

    private deleteNodeFromNodeMap(id: number) {
        let updatedNodeMap = new Map<Node, Node[]>();
        this.nodeMap.forEach((v, k) => {
            if (k.nodeDto.id !== id) {
                let updatedNodes = v.filter(e => e.nodeDto.id !== id);
                updatedNodeMap.set(k, updatedNodes);
            }
        });
        this.nodeMap = updatedNodeMap;
    }
}